<template>
  <div class="article-item">
<van-cell :to="`/article/${7817}`">
<!-- article.art_id -->
<!-- 标题信息 -->
  <template #title>
<!-- van-multi-ellipsis--l2这是vant自带的文字省略 -->
    <div class="title van-multi-ellipsis--l2">
       {{article.title}}
    </div>
  </template>

<!-- 底部信息 ---放置图片和文字 -->
  <template #label>
<!-- 图片盒子 -->
    <div v-if="article.cover.type ===3" class="cover-wrap">
       <div class="cover-item" v-for="(img,index) in article.cover.images" :key="index">
          <van-image class="right-image" :src="img"></van-image>
       </div>
    </div>
<!-- 文字盒子 -->
    <div class="label-info-wrap">
    <span>{{article.aut_name}}</span>
    <span>{{article.comm_count}}评论</span>
    <!-- relativeTime相对时间显示 -->
    <span>{{article.pubdate | relativeTime}}</span>
    </div>
  </template>
  <!-- 通过default插槽来展示当张图片的样式表现 -->
  <template #default v-if="article.cover.type === 1">
    <van-image class="right-image" :src="article.cover.images[0]">
    </van-image>
  </template>
</van-cell>
  </div>
</template>

<script>
export default {
  name: 'ArticleItem',
  props: {
    article: {
      type: Object,
      dafault: () => ({})
    }
  },
  data () {
    return {

    }
  },
  methods: {

  }
}
</script>

<style lang="less" scoped>
.article-item{
  .title{
    font-size: 32px;
    color: #3a3a3a;
     }
   // 修改右边盒子的样式结构 -- 单张图片展示结构
     .van-cell__value{
   flex: unset;
   width: 230px;
   height: 146px;
   padding-left: 25px;
}
.right-image{
  width: 100%;
  height: 100%;
}
/* 底部文字样式 */
.label-info-wrap span{
  font-size: 22px;
  color:#b4b4b4;
  margin-right: 20px;
}
// 多图样式
.cover-wrap{
  display: flex;
  padding-top: 30px;
  .cover-item{
    //每张图片各占1/3
    flex:1;
    height: 146px;
    //  &:not(:last-child)可以让最后一张图片没有右边距
    &:not(:last-child){
      margin-right: 20px;
    }
  }
}
}
</style>
