<template>
  <div class="home-container">
    <van-nav-bar title="标题" class="page-nav-bar" fixed>
    <template #title>
    <van-button round type="info" icon="search"
    class="search-btn" to="/search">搜索</van-button>
  </template>
    </van-nav-bar>

    <!-- tab频道列表 -->
    <!--通常就设置这四个就可以了
      v-model:绑定当前激活标签对应的索引值，默认启用第一个
      animated：开启切换标签内容时的转场动画
      swipeable：开启滑动切换标签页（只作用于移动端）
      swipe-threshold：设置最低的滑动标签数，越小越好
     -->
   <van-tabs v-model="active" class="channel-tabs" animated swipeable swipe-threshold="2">
  <van-tab
  v-for="obj in channels" :key="obj.id"
  :title="obj.name">
  <!-- 此处需要放一个组件，用组件的方法渲染页面。 -->
  <articleList :channel="obj"></articleList>
  </van-tab>

  <template #nav-right>
    <!-- 设置这个盒子的目的是为了不让最后一个盒子被字体图标遮住（占位） -->
    <div class="placheolder"></div>
    <div class="hamburger-btn" @click="show = true">
      <i class="toutiao toutiao-gengduo"></i>
    </div>
  </template>
</van-tabs>
<!-- 频道编辑底部弹出 -->
<van-popup v-model="show" position="top" :style="{ height: '100%' }" closeable
  close-icon-position="top-left" class="edit-popup">
  <ChannelEdit :myChannels="channels" :active="active" :allchannel="allchannel" @update-active="onUpdateActive"></ChannelEdit>
  </van-popup>
  </div>
</template>

<script>
import { getUserChannelsAPI, getAllChannelsAPI } from '../../api/index.js'
import articleList from './components/article-list.vue'
import ChannelEdit from './components/channel-edit.vue'
import { mapState } from 'vuex'
import { getItem } from '../../utils/storage.js'
export default {
  name: 'HomeVue',
  components: {
    articleList,
    ChannelEdit
  },
  data () {
    return {
      active: 0,
      channels: [],
      show: false,
      allchannel: [] // 全部频道列表
    }
  },
  computed: {
    ...mapState(['user'])
  },
  created () {
    // 调用获取频道类别方法
    this.loadChannels()
    // 调用获取全部频道列表的方法
    this.loadAllchannels()
  },
  methods: {
    // 获取频道类别方法-获取用户当前频道列表
    async loadChannels () {
      try {
        // 以后调用接口获取返回值时，可以使用解构赋值的方法拿到真实的返回值{ data: res }
        /*  const { data: res } = await getUserChannelsAPI()
        this.channels = res.data.channels */
        /*  if (this.user) {
          // 已登录状态调用接口
          const { data: res } = await getUserChannelsAPI()
          this.channels = res.data.channels
        } else {
          // 未登录状态
          // 获取本地频道列表
          const loaclChannels = getItem('TOUTIAO_CHANNELS')
          if (loaclChannels) {
            // 有本地列表
            this.channels = loaclChannels
          } else {
            // 没有本地列表
            const { data: res } = await getUserChannelsAPI()
            this.channels = res.data.channels
          }
        } */
        // 上面的优化
        let channelstemp = [] // 临时数据列表，最后将其赋值给this，channels
        const loaclChannels = getItem('TOUTIAO_CHANNELS')
        if (this.user || !loaclChannels) {
          // 登录或者本地没有存储时，获取后端数据
          const { data: res } = await getUserChannelsAPI()
          channelstemp = res.data.channels
        } else {
          // 除上面外，均可以使用本地列表数据
          channelstemp = loaclChannels
        }
        this.channels = channelstemp
      } catch (error) {
        console.log('获取频道列表数据失败')
      }
    },
    // 获取全部频道列表的方法
    async loadAllchannels () {
      const { data: res } = await getAllChannelsAPI()
      try {
        this.allchannel = res.data.channels
        console.log(res)
      } catch (errors) {
        this.$toast('获取全部频道数据失败')
      }
    },
    // 自定义组件事件
    onUpdateActive (index, isShow = false) {
      // 1.修改active当前选中的频道
      this.active = index
      // 2.将弹出层关闭
      this.show = isShow
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding-top: 174px;
  padding-bottom: 100px;

  :deep(.van-nav-bar__title){
    max-width: unset;
  }
  /* /deep/ 是vue2的常用写法，在vue3中不可使用， :deep()可以适用于vue2 3 */
  .search-btn {
    background-color: #5babfb;
    border: none;
    width: 555px;
    height: 64px;
    font-size: 28px;
    .van-icon {
      color: #fff;
      font-size: 28px;
    }
  }

  :deep(.channel-tabs) {
    .van-tabs__wrap {
      height: 82px;
      position: fixed;
      top: 92px;
      z-index: 1;
      left: 0;
      right: 0;
    }
      .van-tab {
      border-right: 1px solid #edeff3;
      min-width: 200px;
      font-size: 30px;
      color: #777;
    }

    .van-tab--active {
      color: #333;
      font-weight: 700;
    }

    .van-tabs__line {
      width: 32px;
      background-color: #5babfb;
    }
      .placheolder{
        width: 66px;
        flex-shrink: 0;/* flex布局需要用 flex-shrink取消该元素在 */
      }

        .hamburger-btn {
      position: fixed;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 66px;
      height: 82px;
      background-color: rgba(255, 255, 255, 0.9);

      &:before {
        content: '';
        position: absolute;
        left: 0;
        width: 2px;
        height: 58px;
        background-image: url(../../assets/gradient-gray-line.png);
        background-size: contain;
      }
    }

  }

  // 点击弹出层
  .edit-popup{
    box-sizing: border-box;
    padding-top: 100px;
  }
}
</style>
