<template>
  <div class="channel-edit">
<!-- 我的频道 -->
<van-cell-group>
  <van-cell title="我的频道">
    <template #default>
      <van-button type="danger" round size="mini" plain class="edit-btn" @click="isEditShow= !isEditShow">
       {{ isEditShow ? '完成':'编辑'}}
    </van-button>
   </template>
  </van-cell>
  <!-- 我的频道选项 -->
  <van-grid :gutter="10" class="my-grid">
<van-grid-item class="channel-item" :key="obj.id" v-for="(obj,index) in myChannels" :text="obj.name"
:class='{ active:active ===index}' @click="onMychannels(obj,index)">
<template #icon>
  <van-icon name='close' v-show="isEditShow && index !== 0"/>
</template>
</van-grid-item>
  </van-grid>
  <!-- 频道推荐选项 -->
<van-cell title="频道推荐"></van-cell>
  <!-- 推荐选项 -->
  <van-grid :gutter="10" class="recommend-grid">
<van-grid-item class="channel-item" :key="obj.id" v-for="obj in recommendChannel" :text="obj.name" icon='plus' @click='onAddChannel(obj)'/>
  </van-grid>
</van-cell-group>
  </div>
</template>

<script>
/* 通过点击编辑按钮
1.显示隐藏×
2.推荐频道不可删除
3.点击编辑按钮实现切换操作
*/
/*
 当非编辑状态时，实现切换频道并关闭弹出层
 编辑状态时，删除频道(splice会改变原数组)
 当删除
*/
import { mapState } from 'vuex'
import { setItem } from '../../../utils/storage.js'
import { addUserChannelAPI, deleteUserChannelAPI } from '../../../api/index.js'

export default {
  name: 'ChannelEdit',
  props: {
    // 当前用户频道
    myChannels: {
      type: Array,
      default: () => []
    },
    // 当前选中频道
    active: {
      type: Number,
      default: 0
    },
    // 所有频道列表
    allchannel: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      myChannelsClone: this.myChannels,
      isEditShow: false
    }
  },
  // 目标：处理推荐频道：我的频道 + 推荐频道 = 全部频道
  // 数组过滤：computed计算属性
  computed: {
    ...mapState(['user']),
    // recommendChannel () {
    //   // 通过filter遍历数组的方式，能够获取到每一个全部频道中的元素
    //   const newList = this.allchannel.filter(channel => {
    //     // 将该元素与我的频道列表中的元素做对比，如果我的频道有相同的元素id时，该元素被过滤掉
    //     /* 查找数组中的某一特定元素的方法
    //     find 返回查找到的那个元素，如果没有找到返回undefind
    //     findIndex 返回查找到的那个元素的索引，如果没有找到返回-1
    //     indexof 返回查找到的那个元素的索引，如果没有找到返回-1(只能是简单数据类型)
    //     includes 判断数组有无该元素，有返回true，无返回false
    //     */
    //     return !this.myChannels.find(myChannels => {
    //       return myChannels.id === channel.id
    //     })
    //   })
    //   return newList
    // }
    // 方法2：使用lodash的方法完成过滤
    recommendChannel () {
      return this.$_.differenceBy(this.allchannel, this.myChannels, 'id')
    }
  },
  methods: {
    // 添加频道(因为当计算属性中所使用的变量发生改变时，会自动计算。所有我的频道改变，)
    async onAddChannel (newChannel) {
      this.myChannelsClone.push(newChannel)
      // 数据持久化处理
      if (this.user) {
      // 已登录状态调用接口
        try {
          const channel = [
            {
              // seq后端数据
              id: newChannel.id,
              seq: this.myChannels.length - 1
            }
          ]
          // 我们只需要关心接口有没有调用成功，并不需要返回值的内容---不需要res
          await addUserChannelAPI({
            // channels后端数据
            channels: channel
          })
          this.$toast('添加成功')
        } catch (error) {
          this.$toast('添加失败')
        }
      } else {
        // 未登录状态，存储在本地
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    },

    // 点击我的频道中的方法
    onMychannels (channel, index) {
      if (this.isEditShow) {
        // 编辑状态删除频道
        // 3.判断是否为推荐
        if (index === 0) return
        // 1.删除选中的频道---需要用克隆体
        this.myChannelsClone.splice(index, 1)
        // 2.让激活的频道减1
        if (index <= this.active) {
          this.$emit('update-active', this.active - 1, true)
        }
        // 删除数据持久化
        this.deleteChannel(channel)
      } else {
        // 非编辑状态--切换频道
        this.$emit('update-active', index)
      }
    },
    async deleteChannel (delChannel) {
      if (this.user) {
        // 删除指定用户列表
        try {
          await deleteUserChannelAPI(delChannel.id)
        } catch (error) {
          this.$toast('删除频道失败失败')
        }
      } else {
        // 未登录状态，存储在本地
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    }
  }

}
</script>

<style lang="less" scoped>
.channel-edit{
  white-space: nowrap;
  flex-direction: row;
    .van-cell__title{
        height: 58px;
        line-height: 58px;
        font-size: 32px;
        color: #333;
    }
.edit-btn{
    width: 100px;
    height: 48px;
    font-size: 26px;
    color:#f85959 ;
    border: #f85959;
 }
 .channel-item{
height: 80px;
width: 160px;
//设置方块背景颜色
:deep(.van-grid-item__content){
background-color: #f5f5f6;
//设置方块字体的样式
.van-grid-item__text{
color:#222;
font-size: 28px;
margin-top: 0px;
}

}
 }
 //我的频道关闭（×）图标样式的处理
 :deep(.my-grid){
.van-grid-item__content{
.van-grid-item__icon-wrapper{
position: absolute;
top:-10px;
right: -10px;
font-size: 30px;
color: #cacaca;
z-index:2
}
}
// 设置动态高亮样式
.active{
  .van-grid-item__text{
    color:red
  }
}
 }
 //频道推荐 + 图标的处理
:deep(.recommend-grid){
 .van-grid-item__content{
  //flex布局的横向布局 ，所有子元素不会进行换行
  white-space: nowrap;
  flex-direction: row;
    .van-icon-plus{
      font-size: 28px;
      margin-right: 6px;
    }
 }
}
}

</style>
