<template>
  <div class="articleList-container">
    <!-- van-pull-refresh下拉刷新 -->
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" :success-text="refreshingText" success-duration="1500">
<van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  :error.sync = "error"
  error-text="请求失败，点击重新加载"
  @load="onLoad"
>
  <!-- <van-cell v-for="(item,index) in list" :key="index" :title="item.title" /> -->
  <ArticleItem v-for="(item,index) in list" :key="index" :article="item"></ArticleItem>
</van-list>
 </van-pull-refresh>
  </div>
</template>

<script>
import { getArticlesAPI } from '../../../api/index.js'
import ArticleItem from '../../../components/article-item.vue'
export default {
  name: 'ArticleList',
  components: {
    ArticleItem
  },
  props: {
    channel: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      list: [], // 存储列表数据的数组
      loading: false, // 控制加载状态的变量。如果loading为true时，将不会触发load事件
      finished: false, // 控制数据加载结束的标志。当finished为true时，列表vant-list将不会触发load，同时会显示finished-text的内容到页面底部
      timesTamp: 0, // 请求下一页的数据时间戳
      error: false, // 控制列表加载失败的提示状态 --当error为true时,load事件不会触发
      refreshing: false, // 下拉刷新状态标记
      refreshingText: '更新成功' // 下拉刷新的提示文本
    }
  },
  methods: {
    // 在初始化时，以及滚动到底部时会触发load事件绑定的onload方法
    async onLoad () {
      try {
        // 1.请求接口数据
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id,
          timestamp: this.timesTamp || Date.now() // 请求新的推荐数据时间戳，如果已请求过，那么使用接口返回的那个时间戳
        })
        // 2.把请求结束的数据拼接到数组list最后
        console.log(res)
        this.list.push(...res.data.results)
        // 3.本次数据加载结束一行，需要把加载状态的loading设置为结束状态
        // loading关闭后才会查房下一次的load事件
        this.loading = false
        // 4.通过某个条件判断出是否全部数据加载完成，需要将finished设置为true，在此之后1load事件将永不触发
        if (res.data.results.length === 0) {
          this.finished = true
        } else {
          this.timesTamp = res.data.pre_timestamp
        }
      } catch (error) {
        // 展示错误提示状态
        this.error = true
        // 请求失败时
        this.loading = false
        this.$toast('接口请求失败，请稍后重试')
      }
    },
    // 当下拉刷新时触发
    async onRefresh () {
      try {
        // 1.请求接口数据
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id,
          timestamp: Date.now() // 请求新的推荐数据时间戳，如果已请求过，那么使用接口返回的那个时间戳
        })
        // 2.把请求结束的数据拼接到数组list最前面
        this.list.unshift(...res.data.results)
        // 3.本次数据加载结束一行，需要把加载状态的loading设置为结束状态
        // loading关闭后才会查询下一次的load事件
        this.refreshing = false
        // 更新下拉成功时的提示内容
        this.refreshingText = `刷新成功，更新了${res.data.results.length}条数据`
      } catch (error) {
        // 关闭刷新加载状态，提示失败信息
        this.refreshingText = '刷新失败，请重试'
        this.refreshing = false
      }
    }
  }
}
</script>

<style lang="less" scoped>
.articleList-container{
 //通过视口单位vw/vh来设置。（通常用在移动端布局上，不会受到父元素影响）
// 添加这个可以保证不让内容区域进行跳转
height:79vh;/* (667px-(46px + 41px +50px))/100 */
  overflow-y: auto ;
}
</style>
